import React, { useContext, useEffect } from 'react';
import { graphContext } from './Canvas';

export interface NodeType {
    uuid?: number;
    label: string;
    x: number;
    y: number;
}

export const Node: React.FC<NodeType> = (props) => {
    const { label, x, y, uuid } = props;
    const [myGraph, setGraph] = useContext(graphContext);
    const canvas: any = document?.getElementById('myCanvas');
    useEffect(() => {
        console.log('rendered');
        const ctx: any = canvas.getContext('2d');

        ctx.beginPath();
        ctx.arc(x, y, 50, 0, Math.PI * 2, true);
        ctx.lineWidth = 2;
        ctx.stroke();
        ctx.font = '30px Arial';
        ctx.fillText(label, x - 10, y + 10);
        let docs = document.querySelectorAll('[id^="node"]');
        console.log(docs.length);
    }, []);

    return <div id={`node ${label} ${x} ${y}`}></div>;
};
